body {
  background-color: $primary-bg-color;
  color: $primary-text-color;
  font-family: 'Roboto', sans-serif;

  font-size: 12px;
  line-height: 1.33;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
