.modal-close-btn {
    position: absolute;
    width: 30px;
    height: 30px;

    top: 0;
    right: 0;
    margin-top: 10px;
    margin-right: 10px;

    border-style: none;

    background-color: white;
}
.modal-icon-close {
    fill: black;
}

.modal-wraper {
    padding: 50px 20px 40px 20px;
    @media screen and (min-width: 768px) {
        padding: 40px 30px;
        display: flex;
        flex-basis: auto;
        justify-content: space-around;
    }
    @media screen and (min-width: 1024px) {
        padding: 50px 30px 40px 30px;
    }
}

.modal-movie-cover {
    margin-bottom: 30px;
    min-width: 240px;
    height: 100%;
    border-radius: 5px;
    @media screen and (min-width: 768px) {
        max-width: 395px;
        
    }
}
.movie-content-wraper {
    @media screen and (min-width: 768px) {
        display: flex;
        flex-direction: column;
    }
}

.modal-movie-headline {
    margin-bottom: 20px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;

    color: #000000;
}
.modal-movie-stats-container {
    display: flex;
    margin-bottom: 20px;
}
.modal-movie-stats-titles {
    width: 50%;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;

    color: #8C8C8C;
}

.modal-movie-stats-data {
    width: 50%;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
}
.modal-movie-stats-titles li:not(:last-child), 
.modal-movie-stats-data li:not(:last-child) {
    margin-bottom: 10px;
} 

.modal-movie-stats-data .vote {
    padding: 1px 10px;
    color: white;
    text-align: center;
    background: #FF6B01;
    border-radius: 5px;
}
.modal-movie-stats-data .votes {
    padding: 1px 10px;

    text-align: center;
    background: #F7F7F7;
    border-radius: 5px;
}
.txt2uppcase {
    text-transform: uppercase;
}
.modal-movie-about-headline {
    padding-bottom: 20px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
}

.modal-movie-about-descr {
    margin-bottom: 20px;

    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
}
.modal-movie-btn-wraper {
    display: flex;
    align-items: center;
    justify-content: center;
}
.add2watch-btn {
    margin-right: 15px;
}
.add2watch-btn, .add2queue-btn {
    width: 112px;
    height: 44px;

    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;

    border: 1px solid #000000;
    box-sizing: border-box;
    border-radius: 5px;

    background: white;

    @media screen and (min-width: 1024px) {
        width: 136px;
    }
}
.active-btn {
    border: none;
    color: white;
    text-align: center;
    background: #FF6B01;
}